import './App.css';
import { Routes, Route, useLocation } from 'react-router-dom';
import { AnimatePresence } from 'framer-motion';
import React, { createContext, useState } from "react"
import ReactSwitch from 'react-switch';

import Navbar from './Component/Navbar';
import Footer from './Component/Footer';
import Home from './Pages/Home';
import Project from './Pages/Project';
import About from './Pages/About';
import Skill from './Pages/Skill';
import Contact from './Pages/Contact';
import Cursor from './Cursor';

export const ThemeContext = createContext(null);

function App() {
  const [theme, setTheme] = useState("light"); //theme    boolean  id  . , setTheme current   toggleTheme   .

  const toggleTheme = () => {
    setTheme((curr) => (curr === "light" ? "dark" : "light")); // setTheme current   current  light   boolean dark  light  .
  };

  const location = useLocation();

  return(
    <ThemeContext.Provider value={{ theme, toggleTheme }}>
    <div className="App" id={theme}>
    <Cursor />
      <Navbar />   
      <div className="switch">
      <label> {theme === "light" ? "Light Mode" : "Dark Mode"}</label>
      <ReactSwitch onChange={toggleTheme} checked={theme === "dark"} />
      </div>
      <AnimatePresence mode="wait">
        <Routes location={location} key={location.pathname}>
            <Route path="/portfolio" element={<Home/>} />
            <Route path="/about" element={<About/>} />
            <Route path="/project" element={<Project/>} />
            <Route path="/skill" element={<Skill/>} />
            <Route path="/contact" element={<Contact/>} />
          </Routes>
        <Footer />
      </AnimatePresence>
    </div>
    </ThemeContext.Provider>
  );//label  theme ===   light  boolean  Light Mode  Dark Mode  . 
    //ReactSwitch toggleTeheme  checked ReactSwitch  .
}

export default App;